/* *************************************************************************** social icon**************************************************** */

.member-icons {
  padding: 0;
  list-style: none;
  margin: 1em;
}

.member-icons li {
  display: inline-block;
  margin: 0.15em;
  position: relative;
  font-size: 1em;
}

.member-icons i {
  color: #fff;
  position: absolute;
  top: 0.95em;
  left: 0.96em;
  transition: all 265ms ease-out;
}

.member-icons a {
  display: inline-block;
}

.member-icons a:before {
  transform: scale(1);
  -ms-transform: scale(1);
  -webkit-transform: scale(1);
  content: " ";
  width: 45px;
  height: 45px;
  border-radius: 100%;
  display: block;
  background: linear-gradient(45deg, #f05b36, #393a98);
  transition: all 265ms ease-out;
}

.member-icons a:hover:before {
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: all 265ms ease-in;
}

.member-icons a:hover i {
  transform: scale(1.7);
  -ms-transform: scale(1.7);
  -webkit-transform: scale(1.7);
  color: #393a98;
  background: -webkit-linear-gradient(45deg, #f05b36, #393a98);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 265ms ease-in;
}

